'use strict';

const populationList = [...document.querySelectorAll('span.population')];
const totalPopulationElement = document.querySelector('span.total-population');
const avgPopulationElement = document.querySelector('span.average-population');
const clearList = populationList
  .map(item => Number(item.innerHTML.replace(/\,/g, '')));

const total = clearList.reduce((a, b) => a + b, 0);
const avg = total / clearList.length;

totalPopulationElement.innerHTML = total;
avgPopulationElement.innerHTML = avg;
